import React, { useState , useEffect } from 'react'
import {Personaje} from './Personaje.jsx'

export const Boton = () => {

    const [personaje, setPersonaje] = useState([])

    const roll = (min, max, floatFlag) => {
        let r = Math.random() * (max - min) + min
        return floatFlag ? r : Math.floor(r)
    }
    
    const obtenerDatos = async () =>{
        
        const id = roll(1,672)
       
        const dataFetch = await fetch(`https://rickandmortyapi.com/api/character/${id}`);
        const dataPersonaje = await dataFetch.json();
       
        setPersonaje(dataPersonaje);
        
    }
    

    useEffect(() => {
        console.log('effect');
        document.title = 'Rick and Morty'
        setPersonaje([])
     }, [])
 


    
    
    

    return (
        <div>
                
                { personaje.length === 0 ? (
                    <button onClick={obtenerDatos} className="boton-buscar">Buscar Personaje</button>
                ) : (
                    <>
                    <button onClick={obtenerDatos} className="boton-buscar">Ver mas!</button>
                    <Personaje data={{personaje}} />
                    </>
                )
                    

                }
                
        </div>
    )
}
